import React, { Component } from 'react';

import SecondaryPage from '../SecondaryPage/SecondaryPage';

class AboutPage extends Component {
	render() {
		return (
			<SecondaryPage
				headline="About"
				className="AboutPage">
				AboutPage
			</SecondaryPage>
		);
	}
}

export default AboutPage;



