import React, { Component } from 'react';

import SecondaryPage from '../SecondaryPage/SecondaryPage';

class TourPage extends Component {
	render() {
		return (
			<SecondaryPage
				headline="Tour"
				className="TourPage">
				TourPage
			</SecondaryPage>
		);
	}
}

export default TourPage;



